$darker-blue: #0B0C10;
$dark-blue: #1F2833;
$grey: #C5C6C7;
$light-blue: #45A29E;
$lighter-blue: #66FCF1;

$dark-spotify: #222222;
$tomato: #eb583a;
$true-tomato: #b85341;
$dark-tomato: #963c2e;
$green: #81ad98;
$dark-bege: #45A29E;
$bege: #66FCF1;


html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
  font-size: 14px;
  background-color: whitesmoke;
  // display: flex;
  flex-direction: row;
}

.app {
  height: 100%;
  width: 100%;
}

.canvas-container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
}

.canvas {
  height: 100%;
  width: 100%;
  // filter: grayscale(1);
}

.canvas-group {
  height: 100%;
  display: flex;
  flex-direction: row;
}

.subject-canvas {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  flex-shrink: 0;
  -webkit-touch-callout: none;
  background-color: whitesmoke;
  overflow: hidden;
}

.who-am-i {
  background-color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.web-dev {
  background-color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.game-dev {
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.black-bg {
  background-color: rgba(black, 0.3);
}

.blur-bg {
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

.card {
  padding: 1ch;
  border-radius: 1ch;
}

.container {
  display: flex;
  flex-direction: row;
  max-width: 80vw;
  max-height: 80vh;
  margin-bottom: 10ch;
  z-index: 1;
}

.container-wd {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 160ch;
  max-width: 80vw;
  max-height: 80vh;
  margin-bottom: 10ch;
  z-index: 1;
}

.container-gd {
  display: flex;
  flex-direction: column;
  max-width: 140ch;
  max-height: 80vh;
  margin-bottom: 8ch;
  z-index: 1;
}

.left-container {
  // flex: 1;
  width: min-content;
  display: flex;
  align-items: center;
}

.three-containers {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 4ch;
  pointer-events: none;
  width: 50ch;
}

.icon-container {
  background-color: rgba(black, 0.1);
  height: 200px;
  width: 200px;
  border-radius: 100px;
  margin-bottom: 4ch;
  backdrop-filter: blur(4px);
  align-self: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.column-title {
  font-size: 6ch;
  font-weight: bold;
  margin-bottom: 0.5ch;
  text-align: center;
}

.column-about {
  font-size: 3ch;
  font-weight: thin;
  margin-bottom: 0.5ch;
}

.column-about-small {
  font-size: 2ch;
  font-weight: thin;
}

.text-container {
  display: flex;
  flex-direction: row;
}

.name-text {
  font-size: 10ch;
  font-weight: bold;
  margin-bottom: 0.5ch;
}

.title-text {
  font-size: 8ch;
  font-weight: bold;
  margin-bottom: 0.5ch;
}

.about-text {
  font-size: 3ch;
  font-weight: thin;
  margin-bottom: 0.5ch;
}

.about-text-small {
  font-size: 2ch;
  font-weight: thin;
}

.justify {
  text-align: justify;
}

.about-list {
  margin: 0 3ch;
}

.about-list-item {
  margin: 3ch 0;
}

.script-bf-box {
  width: 5px;
  height: 300px;
  background: $dark-tomato;
  margin: 5px;
  flex-shrink: 0;
  will-change: transform;
}

.particle {
  position: absolute;
  width: 5px;
  height: 5px;
  background: $dark-tomato;
  flex-shrink: 0;
  will-change: transform;
}

.button {
  color: white;
  width: 14vw;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  // border-bottom: 1px solid white;
  mix-blend-mode: difference;
  backdrop-filter: blur(8px);
  // -webkit-backdrop-filter: blur(8px);
  background-color: rgba(black, 0.9);
  border-radius: 0.5ch;
  padding: 1ch 2ch;
  font-size: large;
  letter-spacing: 0.3ch;
}

.button:hover {
  cursor: pointer;
  background-color: rgba(55, 55, 55, 0.7);
}

.button-container {
  width: 100%;
  padding: 0px 10vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.footer {
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 8ch;
}

.video {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(0.5);
}

.trails-main {
  // position: relative;
  // width: 100%;
  // height: 100%;
  overflow: hidden;
  // cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.trails-text {
  // position: relative;
  // width: 100%;
  // height: 110px;
  // line-height: 110px;
  color: black;
  font-size: 8em;
  font-weight: 800;
  letter-spacing: -9px;
  will-change: transform, opacity;
  overflow: hidden;
}

.trails-text > div {
  overflow: hidden;
}


.translate[sceneId='0'] {
  transform: translateX(0em);
  transition: transform 0.5s ease-in-out;
}
.translate[sceneId='1'] {
  transform: translateX(-100vw);
  transition: transform 0.5s ease-in-out;
}
.translate[sceneId='2'] {
  transform: translateX(-200vw);
  transition: transform 0.5s ease-in-out;
}
.translate[sceneId='3'] {
  transform: translateX(-300vw);
  transition: transform 0.5s ease-in-out;
}
.translate[sceneId='4'] {
  transform: translateX(-400vw);
  transition: transform 0.5s ease-in-out;
}